@keyframes AnimationName {
  0%{background-position:0% 50%}
  50%{background-position:100% 50%}
  100%{background-position:0% 50%}
}

.App {
  font-family: "Gotham SSm A","Gotham SSm B",Montserrat,Helvetica,sans-serif;
  font-size: 16px;
  color: #222;
  display: grid;
  grid-template-columns: 1;
  grid-template-rows: 90% 10%;
  width: 100vw;
  height: 100vh;

  .skip-link {
    position: absolute;
    top: -40px;
    left: 0;
    background: #000000;
    color: white;
    padding: 8px;
    z-index: 100;

    &:focus {
      top: 0;
    }
  }

  main {
    position: relative;
    &::before {
      content:"";
      display: block;
      height: 5px;
      width: 100%;
      background: linear-gradient(270deg, #0085b2, #ff007f, #000000);
      background-size: 600% 600%;
      animation: AnimationName 30s ease infinite;
    }

    article {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 90%;
      font-weight: 700;
      line-height: 1.5;

      .profile {
        max-height: 128px;
        border-radius: 50%;
      }

      footer {
        font-size: large;
        p {
          margin: .5em 0;
        }
      }

      @media (min-width: 1024px) {
        font-size: 3em;
        width: 50%;
      }
    }

    a {
      position: relative;
      text-decoration: none;
      display: inline-block;
      color: inherit;
      padding: 0px 0px .5em;
      transition: color ease 0.3s;

      @media (min-width: 1024px) {
        padding: 0px 0px .2em;
      }

      &::after, &::before {
        content: '';
        position: absolute;
        background-color: #222;
        z-index: -1;
        height: 2px;

        @media (min-width: 1024px) {
          height: 4px;
        }
      }

      &::after {
        width: 100%;
        left: 0;
        bottom: 0;
        transition: all ease 0.6s;
      }

      &::before {
        width: 0%;
        left: 0;
        bottom: 0;
        transition: width ease 0.4s;
      }

      &:hover {
          color:  #0085b2;
          &::before {
            width: 100%;
            background: #0085b2;
          }
  
          &::after {
            left: 100%;
            width: 0%;
            background: linear-gradient(270deg, #0085b2, #ff007f);
            transition: all ease 0.2s;
          }
        
      }
    }
  }

  footer {
    position: relative;

    section {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;

      ul {
        font-size: 1.5em;
        list-style: none;
        list-style: none;
        padding: 0;
        margin: 0;
        li {
          float: left;
          margin: .5em;

          a {
            color: #222;
            &:active, &:visited{
              color: #222;
            }
          }
          &:hover {
            &.tw a i{
              color: #08a0e9;
            }

            &.gt a i{
              color: #4078c0;
            }

            &.in a i{
              color: #0077b5;
            }
          }
        }
      }
      
      @media (min-width: 1024px) {
        width: 50%;
      }

    }

    .disclaimer {
      font-size: small;
      font-style: italic;
    }
  }
}